.card  h1{
    text-align: center;
    
}
.login_top{
    display:flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
    flex-direction: column;
    margin-top: 10%;
    justify-content: center;
    text-align: center;
}

.login_control{
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin: 10px;
}
.login_control label{
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}

.login_control input{
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid rgb(252, 251, 251);
    width:20rem;
    max-width: 100%;
}

.login_actions_submit1{
    text-align: center;
}
.login_actions_register{
    text-align: center;
}
.login_actions_forgotPassword{
    text-align: center;
}